.slider {
    display: flex;
    align-items: center;
    margin: 20px;
  
  }
  
  .slider--zoom {
      opacity: 1;
      transform: scale(100%);
      transition: 2s;
  }


  .slider_image {
    width: 60%;
  }
  
  .slider_content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    text-align: center;
  }
  
  .slider_title {
    text-align: center;
  }
  
@media screen and (max-width: 800px){
  
  .slider{
    flex-direction: column;
    background-color: rgb(236, 236, 236);
  }

  .slider_image{
    width: 100%;
  }
}